import React from "react";
import Page from "components/blog/Page";
import PagePreview from "components/blog/PagePreview";
import { formatDate } from "utils/date";
import { makeUrl, filterPosts, completeJson } from "utils/content";

import CONFIG from "blog/dist/index.json";
import SUMMARY_JSON from "blog/dist/summary.json";

function Index(props) {
  return (
    <div>
      <Page
        siteTitle={`${CONFIG.siteTitle} - Index`}
        heroTitle={CONFIG.siteTitle}
        description={CONFIG.description}
        stylesheets={CONFIG.stylesheets}
        topLinks={CONFIG.topLinks}
        backgroundClass={CONFIG.backgroundClass}
        body={Body({ summaryJson: SUMMARY_JSON })}
        copyright={CONFIG.copyright}
        siteId={CONFIG.siteId}
      />
    </div>
  );
}

function Body(props) {
  const postList = filterPosts(props.summaryJson);
  return (
    <div className="center mw6 pa3 pa4-ns">
      {postList.map((article, i) => {
        const href = makeUrl(article);
        const json = completeJson(article);
        const date = formatDate(article.date);
        return (
          <PagePreview
            title={article.title}
            preview={article.preview}
            date={date}
            json={json}
            href={href}
            key={i}
          />
        );
      })}
    </div>
  );
}

export default Index;
